body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  box-sizing: border-box; }

.Cards_header {
  display: flex;
  justify-content: center;
  align-items: center; }

.Cards_list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr; }

.Cards_list-item {
  width: 160px;
  list-style-type: none;
  background-color: #c5e7f2;
  border: 2px solid grey;
  border-radius: 15px;
  margin-bottom: 15px;
  margin-right: 15px; }

.Poke_img-box {
  display: flex;
  align-items: center;
  justify-content: center; }

.Poke_img {
  height: 160px; }

.Poke_name-box {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid grey;
  border-bottom: none;
  border-left: none;
  border-right: none; }

.Poke_name {
  text-transform: capitalize;
  margin: 0; }

.Poke_types-list {
  border: 2px solid grey;
  border-bottom: none;
  border-left: none;
  border-right: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 20px;
  padding: 5px 5px; }

.Poke_types-item {
  list-style-type: none;
  padding-left: 5px;
  font-size: 12px; }
  .Poke_types-item .item {
    display: inline;
    padding: 2px 5px;
    background-color: grey;
    color: white;
    text-transform: uppercase;
    border-radius: 5px; }

